.IncBtn {
    border: 2px solid #00AAFF;
    grid-area: i;
    font-size: 20px;
    padding: 7px 15px 7px 15px;
    margin-right: 25px;
    width: 100px;
    /*background-color: #EDEEF0;*/
   /* background-color:  #282c34;*/
    background-color:  #00AAFF;
    /*color: white;*/
    color: #282c34;
    border-radius: 5px;
    cursor: pointer;
    font-weight:bold;
    outline: 0;
}

